.OcchiPage {
  .OcchiContentContainer {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: flex-start;
    gap: 5px;
    font-size: 26px;
  }
  .Corsi {
    margin-bottom: 0;
  }
  .Corsi h1,
  h2 {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
    font-family: "font-montserrat-medium", Arial, sans-serif;
    color: rgb(73, 81, 111);
  }
  h3 {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
    font-size: 20px;
    font-family: "font-montserrat-medium", Arial, sans-serif;
    color: rgb(73, 81, 111);
  }
  .Corsi p {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
    font-size: 20px;
    font-family: "font-montserrat-medium", Arial, sans-serif;
    color: rgb(73, 81, 111);
  }
  .OcchiImageContainer {
    width: 50%;
    height: auto;
    margin-bottom: 10;
  }

  .OcchiImageContainer img {
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    width: 100%;
    height: auto;
    border-radius: 3px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  }

  .OcchiTextContainer {
    margin-top: 8px;
    /* text-align: justify; */
    text-align: left;
    margin-right: 25px;
    margin-left: 20px;
    font-family: "font-montserrat-medium", Arial, sans-serif;
    color: rgb(73, 81, 111);
    flex-basis: 50%;
    width: 70%;
  }

  .Paragraphes {
    margin-left: 2%;
    width: 90%;
    font-size: 24px;
  }
  .Paragraphes p {
    letter-spacing: -0.5px;
  }

  .Bvideo {
    max-width: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20%;
    margin-left: 20px;
    margin-top: 20px;
  }
  .Bvideo iframe {
    height: 300px;
    width: 98%;
    border-radius: 10px;
    border: 5px solid #6a91d3;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  }

  @media (max-width: 1224px) {
    .OcchiContentContainer {
      flex-direction: column;
      width: 100%;
    }
    .OcchiImageContainer {
      min-width: 100%;
      justify-content: center;
      align-items: center;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      margin-left: auto;
      margin-right: auto;
    }
    .OcchiImageContainer img {
      margin-top: 10px;
      max-width: 95%;
    }
    .OcchiTextContainer {
      margin-top: 5px;
      min-width: 100%;
      margin: 0 auto !important;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .Paragraphes {
      margin: 10px auto !important;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      flex-direction: column;
    }

    .Paragraphes p {
      word-spacing: normal !important;
      font-size: 12px;
      margin: 0 auto !important;
      font-size: 18px;
      max-width: 100%;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      text-align: left;
      /* text-align: justify; */
      /* word-spacing: normal !important; */
    }
    .Bvideo {
      max-width: 100%;
      margin-right: auto;
      margin-left: auto;
      margin-top: 10px;
      text-align: center;
    }
    .Bvideo iframe {
      height: 200px;
      width: 98%;
    }
  }
}

