.NuvolaPage {
  .NuvolaContentContainer {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    align-items: flex-start;
    gap: 20px;
  }

  .NuvolaLocandina {
    max-width: 50%;
    height: auto;
    margin-bottom: 10;
  }

  .NuvolaLocandina img {
    margin-top: 80px;
    margin-left: 20px;
    margin-right: 20px;
    width: 98%;
    height: auto;
    border-radius: 5px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  }

  .NuvolaTextcontainer {
    font-size: 20px;
    margin-top: 10px;
    text-align: justify;
    max-width: 100%;
    font-family: "font-montserrat-medium", Arial, sans-serif;
  }
  .p1 {
    margin-top: 90px;
    width: 80%;
    text-align: justify;
  }
  .p2 {
    margin-top: 90px;
    width: 80%;
    text-align: justify;
  }
  .p3 {
    margin-top: 90px;
    width: 80%;
    word-spacing: 2px;
    margin-bottom: 150px !important;
    text-align: justify;
  }
  .Bvideo {
    max-width: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px auto;
  }
  .Bvideo iframe {
    min-width: 100% !important;
    min-height: auto !important;
    border-radius: 10px;
    border: 5px solid #6a91d3;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  }
}
.Part2 h1 {
  margin-top: 40px;
  color: rgb(112, 108, 108);
  margin-bottom: 40px;
  font-size: 30px;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "font-montserrat-medium", Arial;
  margin-left: 20%;
}

.Fumetti .ArtistPackage {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 20px;
}

.Fumetti .ArtistPackage img {
  max-width: 50%;
  height: auto;
  margin-left: 20px;
  border-radius: 5px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  margin-bottom: 20px;
}

.Fumetti .ArtistPackage .Nome {
  font-family: "font-montserrat-medium", Arial, sans-serif;
  color: rgb(144, 183, 230);
  font-size: 30px;
  font-weight: bold;
  margin-top: 200px;
  margin-left: -20%;
}

.Fumetti .ArtistPackage .Paragraphe {
  font-family: "font-montserrat-medium", Arial, sans-serif;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  hyphens: auto;
  white-space: normal;
  display: flex;
  flex-basis: 80%;
  color: rgb(86, 84, 84);
  font-size: 18px;
  text-align: justify;
  margin-top: 100px;
  margin-right: 20px;
  width: 40%;
  margin-left: 20%;
}

