.HomePage {
  .Arte {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    color: rgb(104, 117, 126);
    font-family: "font-montserrat-medium", Arial, Helvetica, sans-serif;
  }

  .ParagOne {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    font-size: 20px;
    max-width: 68%;
    margin: 20px auto;
    text-align: left;
    word-spacing: 0.05em;
    overflow-wrap: break-word;
    word-break: keep-all;
    overflow-wrap: break-word;
    hyphens: none;
    font-family: "Montserrat-medium", Arial, Helvetica, sans-serif;
    color: rgb(86, 84, 84);
  }

  .BandeCouleur {
    overflow: auto;
    position: relative;
    margin: 0 auto;
    height: 250px; /* Hauteur de la bande de couleur */
    width: 100%; /* Largeur de la bande de couleur */
    /* background: linear-gradient(160deg, #eab8f0, #f5d97ea3); */
    /* background: rgba(200, 251, 245, 0.831); */
    /* background-color: #90bcf288; */
    background-color: #e7edefb5;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: column; /* Pour descendre le texte */
    justify-content: flex-end; /* Pour descendre le texte */
    align-items: center; /* Pour centrer le texte */
    color: #211e1e; /* Couleur du texte */
    font-size: 12px; /* Taille du texte */
    padding-bottom: 20px; /* Espacement entre le bas de la bande et le texte */
  }

  .Logo {
    border-radius: 3px;
    position: absolute;
    top: 50%;
    left: 50%;
    bottom: 5px;
    transform: translate(-50%, -65%);
    width: 150px;
    height: 150px;
    background-image: url("../../assets/img/Logo2.png");
    background-size: 150px 150px; /* Ajuster la taille de l'image */
    background-position: center 20%;
    background-repeat: no-repeat;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    -webkit-box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  }

  .ParagTwo {
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    font-size: 18px;
    font-family: "font-montserrat-medium", Arial, sans-serif;
    color: #411a04;
  }
  .ImageContainer {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px !important; /* Espace entre les images */
    margin-top: 10px; /* Espace entre la bande de couleur et les images */
  }
  .ImageContainer img {
    width: 10vw; /* Largeur de chaque image */
    height: 10vw; /* Maintient les proportions de l'image */
    object-fit: cover;
    border-radius: 5px; /* Optionnel : ajoute des coins arrondis */
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
  }

  .ImageContainer button {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    font-size: 18px;
    padding: 10px 20px;
    border-radius: 10px;
    cursor: pointer;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2px;
    margin-bottom: 20px;
    background-color: rgb(144, 183, 230);
    color: beige;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.3);
    border: none;
    font-size: 12px;
    text-shadow: 2px 2px 4px #15459e;
  }
  .ImageContainer a {
    text-decoration: none;
  }

  .ImageWrapper {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .ImageCaption {
    margin-top: 10px;
    color: rgb(221, 23, 23);
    font-family: "font-montserrat-medium", Arial, sans-serif;
    font-size: 18px;
    margin-bottom: 5px;
  }
  .ImageTitle {
    font-size: 20px;
    margin-top: 1px;
    font-family: "font-montserrat-medium", Arial, sans-serif;
    color: rgb(113, 171, 242);
  }
}

